import { WpsData } from "./wps_datatypes";
import { Observable, of } from "rxjs";


export interface Cache {
    set(input: object, output: WpsData[]);
    get(input: object): Observable<WpsData[] | null>;
}

export class FakeCache implements Cache {
    set(input: object, output: WpsData[]) {
    }

    get(input: object): Observable<WpsData[] | null> {
        return of(null);
    }
}
